@import "../../../variables.scss";

.input_wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 22rem;

    .title {
        font-weight: bold;
        font-size: 1.15rem;
        color: $white;

        .star {
            color: $red;
            font-size: 1.1rem;
            margin-right: 0.5rem;
        }
    }
    .input {
        outline: none;
        border: none;
        font-size: 1.25rem;
        padding-block: 1rem;
        background: none;
        color: $white;
        width: 15rem;
        transition: 0.15s border-bottom ease-in;
        border-bottom: 2px solid $white;
        text-decoration: none;

        &:focus {
            border-bottom: 2px solid $light-green;
        }

        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus,
        &:-webkit-autofill:active,
        &:-webkit-autofill:valid {
            box-shadow: 0 0 0 30px $secondary-color inset !important;
            font-family: $font-family;
            font-size: 1.25rem;
            color: $white;
            -webkit-text-fill-color: $white;
        }
    }
}
