@import "src/variables";
.edit_appointment_page{
    display: flex;
    width: 70%;
    padding:10px;
    height: 60%;
    margin-top: 20px;
    justify-self: center;
    align-self:center;
}
.account_details {
    width: 100%;
    height: 100%;
    border-radius: 30.8px;
    padding: 2.21rem 2.6rem;
    background-color: $secondary-color;

    .account_details_title {
        font-size: 2rem;
        font-weight: bold;
        padding-bottom: 1rem;
        color: white;
        width: 100%;
        border-bottom: 2px solid $light-gray;
    }

    .details_wrapper {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        height: 80%;

        .detail_wrapper {
            display: flex;
            flex-direction: row;
            width: 50%;
            align-items:center;
            justify-content: center;

            .title {
                font-weight: bold;
                width: fit-content;
                white-space: nowrap;
            }

            .description,
            .title {
                color: white;
                font-size: 1rem;
            }
            .description {
                word-break: break-all;
                overflow-y: scroll;
                text-overflow: ellipsis;
                max-width: 70%;
                margin-left: 10px;
                max-height: 100%;
            }
            .description::-webkit-scrollbar{
                display: none;
            }
        }
    }
}

.close_icon {
    font-size: 2.5rem;
    color: white;
    cursor: pointer;
    position: absolute;
    top: -4rem;
    right: -2rem;
}
.account_details_page {
    width: 90%;
    height: 90%;
    display: flex;
    justify-content: space-evenly;
}
.content {
    margin-top: 4.5rem;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
}
.description_input{
    width:70%;
    height: 80%;
    overflow: scroll;
    resize: none;
    margin-left:10px;
    background-color: $secondary-color;
    outline: none;
    padding: 1rem;
    font-size: 1.1rem;
    color: $white;
    border: 2px solid $light-gray;
    border-radius: 0.5rem;
}
.description_input::-webkit-scrollbar{
    display: none;
}

.input_checkbox{
    margin: 5px;
}
.description_length{
    font-size: 1.15rem;
    color: white;
    align-self:flex-end;
}