@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&display=swap");
@import "../../variables.scss";

.slider_wrapper {
    position: relative;

    .content_wrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 20;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        bottom: 6rem;

        .info_wrapper {
            color: $white;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            flex-direction: column;
            width: 50%;
            height: 60%;
            margin-right: 3.5rem;

            .firm_name {
                font-size: 5rem;
                font-weight: bold;
                text-align: right;
                font-family: "Orbitron", sans-serif;
                border-bottom: 2px $white solid;
                margin-bottom: 3rem;
                animation: fadeIn 0.75s ease-in-out;
            }

            .firm_description {
                text-align: right;
                font-size: 1.35rem;
                margin-bottom: 1.8rem;
                animation: fadeIn 0.75s ease-in-out;
            }

            .scroll_button {
                text-align: center;
                border-radius: 0.2rem;
                font-size: 1.35rem;
                border: 2px $white solid;
                background: none;
                padding: 1.5rem 7rem;
                color: white;
                text-transform: uppercase;
                font-weight: bold;
                cursor: pointer;
                transition: 0.2s background ease-in-out, 0.2s color ease-in-out;
                animation: slideIn 1.75s ease-in-out;

                &:hover {
                    background: $white;
                    color: black;
                }
            }
        }
    }

    .clip {
        filter: blur(5px);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideIn {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }
    50% {
        opacity: 0;
        transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}
    
