@import "../../../../variables.scss";

.appointment_managment_page {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .input_container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: row;
        padding-block: 5rem;
    }
    .table_container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
        animation: 0.5s fadeInFromBottom ease-in-out;

        .table_header {
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: $cyan;
            border-radius: 1rem;
            padding-block: 0.75rem;
            margin-bottom: 0.4rem;
            width: 91rem;

            .cell_wrapper {
                display: flex;
                align-items: center;
                width: 100%;
                height: 100%;

                .cell {
                    font-size: 1.35rem;
                    font-weight: bold;
                    color: $white;
                    width: 20%;
                    text-align: center;
                }
            }
        }

        .table_body {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 91rem;
        }

        .loading {
            margin-top: 14rem;
        }
    }
}

@keyframes fadeInFromBottom {
    from {
        opacity: 0;
        transform: translateY(2.5rem);                
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}